"use client"

import { useEffect, useRef, useState } from "react"
import api from "@/lib/api"
import { X } from "lucide-react"

interface LogsModalProps {
  deployId: string | null
  isOpen: boolean
  onClose: () => void
}

export function LogsModal({ deployId, isOpen, onClose }: LogsModalProps) {
  const [logs, setLogs] = useState<string[]>([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const containerRef = useRef<HTMLDivElement | null>(null)
  const pollRef = useRef<number | null>(null)

  useEffect(() => {
    // reset when opening new modal
    setLogs([])
    setError(null)

    if (!isOpen || !deployId) return

    let cancelled = false
    async function fetchOnce() {
      setLoading(true)
      try {
        const res = await api.get(`/status/${deployId}`)
        const data = res.data
        // logs expected as array of strings
        setLogs(Array.isArray(data.logs) ? data.logs : (data.logs ? [String(data.logs)] : []))
      } catch (e: any) {
        setError("Failed to fetch logs.")
      } finally {
        setLoading(false)
      }
    }

    // initial fetch
    fetchOnce()

    // start polling every 1500ms
    pollRef.current = window.setInterval(fetchOnce, 1500)

    return () => {
      cancelled = true
      if (pollRef.current) {
        clearInterval(pollRef.current)
        pollRef.current = null
      }
    }
  }, [isOpen, deployId])

  // auto scroll to bottom when logs change
  useEffect(() => {
    if (containerRef.current) {
      containerRef.current.scrollTop = containerRef.current.scrollHeight
    }
  }, [logs])

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      {/* backdrop */}
      <div className="absolute inset-0 bg-black/60 backdrop-blur-sm" onClick={onClose}></div>

      <div className="relative z-10 w-[90%] max-w-3xl bg-surface rounded-2xl shadow-xl overflow-hidden">
        <div className="flex items-center justify-between p-4 border-b border-zinc-800">
          <h3 className="text-lg font-semibold">Deployment Logs</h3>
          <button onClick={onClose} className="p-1 rounded hover:bg-zinc-800">
            <X size={18} />
          </button>
        </div>

        <div className="p-4">
          <div className="mb-3 text-sm text-text-secondary">Deploy ID: <code className="px-2 py-1 rounded bg-zinc-900">{deployId}</code></div>

          <div
            ref={containerRef}
            className="h-64 overflow-auto rounded-md border border-zinc-800 bg-black/40 p-3 text-sm font-mono text-text"
            aria-live="polite"
          >
            {loading && logs.length === 0 && <div className="text-text-secondary">Loading logsâ€¦</div>}
            {error && <div className="text-error">{error}</div>}

            {logs.length === 0 && !loading && !error && <div className="text-text-secondary">No logs available yet.</div>}

            {logs.map((ln, idx) => (
              <div key={idx} className="whitespace-pre-wrap py-0.5">
                <span className="text-zinc-400 mr-2 text-xs">[{idx + 1}]</span>
                <span>{ln}</span>
              </div>
            ))}
          </div>

          <div className="mt-3 flex gap-2 justify-end">
            <button
              onClick={() => {
                // force a fetch once by calling status endpoint
                if (!deployId) return
                api.get(`/status/${deployId}`).then((r) => {
                  const data = r.data
                  setLogs(Array.isArray(data.logs) ? data.logs : (data.logs ? [String(data.logs)] : []))
                }).catch(() => setError("Failed to refresh logs."))
              }}
              className="px-3 py-1 rounded bg-accent hover:bg-accent/90 text-background text-sm"
            >
              Refresh now
            </button>
            <button onClick={onClose} className="px-3 py-1 rounded border border-zinc-700 text-sm">Close</button>
          </div>
        </div>
      </div>
    </div>
  )
}

